import React, { useState, useEffect } from 'react';
import { Pagination, Box, useMediaQuery } from '@material-ui/core';
import { useTheme, makeStyles } from '@material-ui/core/styles';

import CardList from '../Card/CardList';
import { FullList } from '../Demo/FullList';

const useStyles = makeStyles((theme) => ({
  paginationStyle: {
    [theme.breakpoints.down('sm')]: { width: 322 },
    [theme.breakpoints.up('sm')]: { width: 342 },
  },
}));

const LatestCard = () => {
  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));

  const classes = useStyles();

  const [cardDataList, setCardDataList] = useState(FullList);
  const [singlePageCardList, setSinglePageCardList] = useState([]);
  const [pageNumber, setPageNumber] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [isLastPage, setIsLastPage] = useState(false);
  const pageSize = 8;

  useEffect(() => {
    if (cardDataList.length > 0) {
      setPageNumber(Math.ceil(cardDataList.length / pageSize));
    }
  }, [cardDataList]);

  useEffect(() => {
    if (cardDataList.length > 0) {
      setSinglePageCardList(
        cardDataList.slice(
          currentPage * pageSize - pageSize,
          Math.ceil(cardDataList.length / pageSize) === currentPage
            ? cardDataList.length
            : currentPage * pageSize
        )
      );
    }
  }, [currentPage, cardDataList]);

  const paginationHandler = (event, value) => {
    setCurrentPage(value);
  };

  const PaginationWrapper = (props) => (
    <Pagination
      boundaryCount={1}
      count={pageNumber}
      page={currentPage}
      // showFirstButton
      // showLastButton
      hidePrevButton={isSmallDevice ? true : false}
      hideNextButton={isSmallDevice ? true : false}
      siblingCount={1}
      size={isSmallDevice ? 'large' : 'medium'}
      variant="text"
      onChange={paginationHandler}
    />
  );

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      <Box className={classes.paginationStyle}>
        <PaginationWrapper />
      </Box>
      <Box>
        <CardList cardDataList={singlePageCardList} />
      </Box>
      <Box className={classes.paginationStyle}>
        <PaginationWrapper />
      </Box>
    </Box>
  );
};

export default LatestCard;
